import emotions from '~/assets/emotionList.js'
export default {
  name: 'EmoticonBox',
  props: {},
  data() {
    return {
      show: false,
      emotionArr: null
    }
  },
  watch: {
    show(val) {
      if (val && this.emotionArr == null) {
        this.loadEmotion()
      }
      this.$emit('iconClick', val)
    }
  },
  created() {},
  mounted() {},
  methods: {
    // 表情点击把名字发给父组件(调用者)
    emoClick(arg) {
      this.$emit('emotionClick', arg)
    },
    // 加载表情
    loadEmotion() {
      const list = emotions.emotionList
      const emotionArr = []
      list.map((item, index) => {
        emotionArr.push({
          name: `[${item}]`,
          url: `<img title="${item}" src="https://res.wx.qq.com/mpres/htmledition/images/icon/emotion/${index}.gif">`
        })
      })
      this.emotionArr = emotionArr
      this.load = true
      console.log('表情盒子加载完成')
    }
  }
}
