import { mapGetters } from 'vuex'
import QRCode from 'qrcode'
import SearchTool from '~/components/SearchTool'
export default {
  name: 'FloatButtonGroup',
  components: {
    SearchTool
  },
  props: {
    appUrl: {
      type: String,
      default: '应用下载地址'
    },
    shareUrl: {
      type: String,
      default: undefined
    },
    search: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      fab: false,
      appQR: '',
      fabShare: false,
      shareQR: ''
    }
  },
  computed: {
    ...mapGetters([
      'footer',
      'content'
    ]),
    right() {
      if (process.server) {
        return 0
      }
      // console.log(this.content)
      const offset = this.content.left + this.content.width + 90
      const r = document.documentElement.clientWidth - offset
      return r > 0 ? r : 0
    }
  },
  watch: {
    appUrl(val) {
      this.calcAppQR()
    },
    shareUrl(val) {
      this.calcShareQR()
    }
  },
  created() {
  },
  mounted() {
    this.calcAppQR()
    this.calcShareQR()
  },
  methods: {
    calcAppQR() {
      if (!this.appUrl) {
        return
      }
      QRCode.toDataURL(this.appUrl)
        .then((url) => {
          this.appQR = url
        })
        .catch((err) => {
          console.error(err)
        })
    },
    calcShareQR() {
      if (!this.shareUrl) {
        return
      }
      QRCode.toDataURL(this.shareUrl)
        .then((url) => {
          this.shareQR = url
        })
        .catch((err) => {
          console.error(err)
        })
    }
  }
}
