// import MusicPlayer from '~/components/MusicPlayer'
import FloatButtonGroup from '~/components/FloatButtonGroup'
import EmoticonBox from '~/components/EmoticonBox'
import EmoticonTextarea from '~/components/EmoticonTextarea'
const limit = 10
export default {
  components: {
    // MusicPlayer,
    FloatButtonGroup, EmoticonBox, EmoticonTextarea
  },
  validate({ params }) {
    // 必须是number类型
    return /^\d+$/.test(params.id)
  },
  asyncData({ params, $axios }) {
    return $axios
      .post(`/getSongDetail`, {
        songId: parseInt(params.id)
      })
      .then((data) => {
        data.lyrics = data.lyric.split('\n')
        return data
      })
  },
  data() {
    return {
      listenUrl: undefined,
      comments: [],
      comment: '',
      loading: false,
      lastFreshTime: 0,
      snackbar: false,
      download: undefined,
      overlay: false,
      sending: false,
      sendSnackbar: false,
      msg: '',
      commentCount: 0,
      singerId: undefined,
      downloadTimes: 0
    }
  },
  computed: {
    lyricStyle() {
      const dark = this.$vuetify.theme.isDark
      // console.log(dark)
      return {
        'border-color': dark ? this.$vuetify.theme.themes.dark.primary : this.$vuetify.theme.themes.light.primary
      }
    },
    singerImg() {
      const names = (this.singerName || '').split('、')
      // if (this.singerName.includes('、')) {
      //   return `/music/pic/固定2.jpg`
      // }
      // return `/music/pic/${this.singerName}2.jpg`
      if (names.length > 2 || names.length === 0) {
        return 'https://cdn.jsdelivr.net/gh/qianxun666/singer-img@master/固定2.jpg'
      }
      return `https://cdn.jsdelivr.net/gh/qianxun666/singer-img@master/${names[0].trim()}2.jpg`
    },
    shareUrl() {
      if (process.server) {
        return ''
      }
      return window.location.href
    }
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll, { passive: true })
  },
  created() {
    // console.log(this.$vuetify)
  },
  mounted() {
    this.getComments()
    window.addEventListener('scroll', this.handleScroll, { passive: true })
  },
  methods: {
    onClickOutside() {
      this.overlay = false
    },
    open(item, index) {
      this.$copyText(item.downloadPass).then(function(e) {
        window.open(item.downloadUrl, '_blank')
      }, function(e) {
        alert('自动复制提取码失败，手动复制')
        console.log(e)
      })
    },
    onTextareaChange(val) {
      // console.log(val)
      if (val.length > 300) {
        const comment = val.slice(0, 305)
        const emo = /\[[\u4E00-\u9FA5]{1,3}\]/gi.exec(comment)
        if (emo.length) {
          this.comment = val
        }
        this.comment = val.slice(0, 300)
      }
    },
    sendComment() {
      if (!this.comment || !this.comment.trim()) {
        return
      }
      this.sending = true
      this.$axios.post('/sendComman', {
        songId: this.$route.params.id,
        contentStr: this.comment
      }).then((data) => {
        this.comments.unshift({
          userName: this.$store.getters.name || '游客',
          contentStr: this.comment,
          time: '刚刚'
        })
        this.commentCount += 1
        this.comment = ''
        this.sending = false
      }).catch((err) => {
        console.log(err)
        this.sending = false
        this.msg = err.response.data.resMsg
        this.sendSnackbar = true
      })
    },
    getComments() {
      // 防抖
      if (this.loading) {
        return
      }
      const now = new Date().getTime()
      if (now - this.lastFreshTime < 1000) {
        return
      }
      this.lastFreshTime = now
      /* 显示加载效果*/
      this.loading = true
      this.$axios
        .post(`/getComman`, {
          songId: parseInt(this.$route.params.id),
          limit,
          offset: this.comments.length
        })
        .then((data) => {
          // console.log(data, data.commans.length === 0)
          this.commentCount = data.count
          if (data.commans.length === 0) {
            this.snackbar = true
          } else {
            this.comments.push(...data.commans)
          }
          this.loading = false
        }).catch((err) => {
          console.log(err)
          this.loading = false
        })
    },
    handleScroll() {
      if (this.snackbar) {
        // 没有了
        return
      }
      // 变量scrollTop是滚动条滚动时,距离顶部的距离
      const scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      // 变量windowHeight是可视区的高度
      const windowHeight = document.documentElement.clientHeight || document.body.clientHeight
      // 变量scrollHeight是滚动条的总高度
      const scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight

      /* 4舍5入 取大 滚动巨鹿 + 可是区域高度 大于 滚动总高度*/
      if (Math.ceil(scrollTop + windowHeight) >= scrollHeight) {
        window.scrollTo(0, scrollHeight - 1)
        // // 清空计时器
        // clearInterval(this.time)
        // this.time = null
        // /* 效果 显示 时常 */
        // this.time = setInterval(this.getComments, 500)
        this.getComments()
      }
    },
    getDownloadUrl(token) {
      console.log(token)
      if (!this.download) {
        const sessionId = this.$store.getters.ssid
        if (sessionId || (typeof token === 'string')) {
          this.$axios
            .post(`/queryAddress`, {
              songId: parseInt(this.$route.params.id),
              token
            })
            .then((data) => {
            // console.log(data)
              this.download = data.adList
              this.downloadTimes++
              this.overlay = true
            }).catch((err) => {
              console.log(err)
            })
        } else {
          const uuid = this.$store.getters.uuid
          this.$clicaptcha({
            songId: parseInt(this.$route.params.id),
            uuid,
            src: '/clicaptcha/', // 后端地址
            success: '验证成功！', // 成功提示，默认为“验证成功！”
            error: '未点中正确区域，请重试！', // 错误提示，默认为“未点中正确区域，请重试！”
            callback: (token) => {
              console.log(token)
              setTimeout(() => {
                this.getDownloadUrl(token)
              }, 100)
            }
          })
        }
      } else {
        this.overlay = true
      }
    },
    play() {
      this.$refs.player.paly()
    },
    thumbUp(item, i) {
      if (item.prize) {
        item.prizeCount--
        item.prize = false
      } else {
        item.prizeCount++
        item.prize = true
      }
    },
    emotionClick(emo) {
      const input = this.$refs.commentText.$refs.input
      input.focus()
      let start = (input && Number(input.selectionStart)) || 0
      let end = (input && Number(input.selectionEnd)) || 0
      if (start > end) {
        const tmp = start
        start = end
        end = tmp
      }
      const left = this.comment.slice(0, start)
      const right = this.comment.slice(end, this.comment.length)
      this.comment = left + emo + right
      if (input) {
        setTimeout(() => {
          input.focus()
          const pos = start + emo.length
          // console.log(start, end, pos, input)
          if (input.setSelectionRange) {
            input.setSelectionRange(pos, pos)
          // input.blur()
          // input.focus()
          } else if (input.createTextRange) {
            const range = input.createRange()
            range.collapse(true)
            range.moveEnd('character', pos)
            range.moveStart('character', pos)
            range.select()
          }
        }, 10)
      }
    },
    iconClick(show) {
      const input = this.$refs.commentText.$refs.input
      // console.log('show', show, input)
      input.focus()
      this.$refs.commentText.focus()
    }
  }
}
